/* Edited for Summit Theme */

.login-register-header {
  @extend .row;
  margin-bottom: $sc-margin-lv4;
}

.login-register-title {
  @extend .col-xs-12;
}

.login-register-title-noregister {
  text-align: center;
}

.login-register-body {
  @extend .row;
}

.login-register-body-colored {
  @extend .login-register-body;

  & .login-register-wrapper-column-login {
    @extend .col-sm-push-3;
  }
}

.login-register-wrapper-column-login,
.login-register-wrapper-column-register {
  @extend .col-xs-12;
  @extend .col-sm-6;
}

.login-register-wrapper-login,
.login-register-wrapper-register {
  @extend .box-column;
}

