.creditcard-edit-button-back {
  @extend .button-back;
}

.creditcard-edit-button-back-icon {
  @extend .button-back-icon;
}

.creditcard-edit {
  @extend .address-edit;
}

.creditcard-edit-form-button-submit {
  @extend .address-edit-form-button-submit;
  margin: $sc-margin-lv3 0 0;
}

.creditcard-edit-form-button-cancel {
  @extend .address-edit-form-button-cancel;
}

.creditcard-edit-form-button-reset {
  @extend .address-edit-form-button-reset;
}

//Modal CreditCardDetailsView
.global-views-modal-content .global-views-modal-content-body {
  .creditcard-edit {
    background: none;
    padding: 0;
    margin: 0;
  }
}

.creditcard-edit-card-selected {
  opacity: 1;
}

.creditcard-edit-card-no-selected {
  opacity: 0.1;
}
